      scaleName: "レ",
    },
    {
      keyColor: "black",
      scaleName: "レ#",
    },
    {
      keyColor: "white",
      scaleName: "ミ",
    },
    {
      keyColor: "white",
      scaleName: "ファ",
    },
    {
      keyColor: "black",
      scaleName: "ファ#",
    },
    {
      keyColor: "white",
      scaleName: "ソ",
    },
    {
      keyColor: "black",
      scaleName: "ソ#",
    },
    {
      keyColor: "white",
      scaleName: "ラ",
    },
    {
      keyColor: "black",
      scaleName: "ラ#",
    },
    {
      keyColor: "white",
